import {
		ajax,
	} from '@/static/js/base.js'
	import api from '@/static/js/api.js'
	import {
		mapGetters
	} from 'vuex'
	export default {
		data() {
			return {
				info:{}
			}
		},
		onLoad(e) {
			this.id = e.id
			ajax({
				url: api.getMySkillInfo,
				type: "POST",
				data: {
					id: this.id
				},
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					this.info = res.data
				}
			})
		},
		methods: {
			submit(){
				ajax({
					url: api.getMySkillDetails,
					type: "POST",
					data: {
						id: this.id
					},
				}).then(res => {
					if (res.status_code == "ok") {
						console.log(res)
						this.info.mobile = res.data.mobile
					}else if(res.status_code == 'error'){
						this.$refs.popup.open();
					}else{
						uni.showToast({
							title: res.data,
							icon:'none'
						})
					}
				})
			},
			close() {
				this.$refs.popup.close();
			},
			navMembership(){
				uni.navigateTo({
					url:'../postPersonal/postPersonal'
				})
			},
		}
	}
